.main-container{
  background-color: #242C40;
  width: 1550px;
  height: 100%;
  position: fixed;
}

.main-container button{
  display: flex;
  justify-content: left;
  align-items: center;
  margin-left: 10%;
  margin-top: 20%;
  padding: 2%;
  padding-left: 3%;
  padding-right: 3%;
  background-image: linear-gradient(-225deg, 
  #32a0e4 0%,
  #18a9f2 48%,
  #4801ff 100%
  );
  border: none;
  outline: none;
  border-radius: 5px;
  color: white;
  font-weight: 600;
  font-size: 15px;
  cursor: pointer;
}

.main-container button:hover{
  background-image:linear-gradient(-225deg, 
  #4801ff 10%,
  #18a9f2 48%,
  #32a0e4 100%
  ); 
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 1);
  
}

.main-container img{
  display: flex;
  justify-content: right;
  align-items: center;
  width: 30%;
  margin-left: 55%;
  margin-top: -20%;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 1);
}

